.tabbar {
       width: 100vw;
       animation: slide 2s;
}
@keyframes slide {
    from {
        margin-left:100%;
        width: 300%;
    }
    to {
        margin-left: 0%;
        width: 100%;
    }
}


.tab {
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
    height: 2em;
    width: 20vw;
    font-family: sans-serif;
    font-size: 1.2em;
    white-space:nowrap;
    border-left: 1px solid black;
    border-bottom: 1px solid black;
    padding: 0;
}
.tab.active {
    border-bottom: none;
    font-weight: bolder;
}

#tab1 {
    background-color: green;
    color: white;
}
#tab2 {
    background-color: purple;
    color: white;
}
#tab3 {
    background-color: skyblue;
    color: black;
}
#tab4 {
    background-color: orange;
    color: black;
}