@import url("https://fonts.googleapis.com/css2?family=Kanit:ital,wght@1,500&display=swap");

header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-family: "Kanit", sans-serif;
  background: #062f4f;
}

header .user-select {
  display: flex;
  flex-direction: row;
}

#logowrapper {
  position: relative;
  margin-left: 1em;
}
.logotext {
  white-space: nowrap;
  font-style: italic;
  font-size: 3em;
  color: red;
  text-shadow: 1px 1px 3px white;
  animation: text_glow 0.7s;
  animation-iteration-count: 1;
  animation-delay: 2.3s;
}

.logotext.tail {
  position: relative;
  left: 40px;
}
.logotext.f {
  position: absolute;
  z-index: 2;
  font-size: 3.2em;
  /* animation: spin 1s;
  animation-delay: 1.2s;
  animation-iteration-count: 1s; */
}
@keyframes text_glow {
  0%   {text-shadow: 1px 1px 3px white;}
  50%  {text-shadow: 3px 3px 9px white;}
  100% {text-shadow: 1px 1px 3px white;}
}
/* @keyframes spin {
  from {transform: rotate(0deg);}
  to   {transform: rotate(360deg);}
} */
#logoman {
  position: relative;
  bottom: 50px;
  padding-left: 8px;
  z-index: 1;
  height: 40px;
  -webkit-filter: drop-shadow(2px 2px 5px red);
  animation: run_in 2s;
  animation-iteration-count: 1;
}
@keyframes run_in {
  0%   {right: 150px;}
  50%  {right: 75px;}
  100% {right: 0px;}
}
